import Vue from 'vue'
import App from '../App'
import Router from 'vue-router'
import home from '@/page/home/home'
import login from '@/page/login/login'
import mobile from '@/page/login/mobile'
import register from '@/page/login/register'
import search from '@/page/search/search'
import hot from '@/page/home/children/hot'
import attention from '@/page/attention/attention'
import quiz from '@/page/quiz/quiz'
import essay from '@/page/essay/essay'
import message from '@/page/message/message'
import user from '@/page/user/user'

Vue.use(Router);

// const home = r => require.ensure([], () => r(require('../page/home/home')), 'home');
// const login = r => require.ensure([], () => r(require('../page/login/login')), 'login');
// const mobile = r => require.ensure([], () => r(require('../page/login/mobile')), 'mobile');
// const register = r => require.ensure([], () => r(require('../page/login/register')), 'register');
export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
        // //home
        {
          path: '/',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          redirect: "/home",
        },
        //
        {
          path: '/home',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          component: home,
          children: [
            {
              path: '',
              meta: {requireAuth: false, keepAlive: false, title: ''},
              component: hot,
            },
            //
            {
              path: '/hot',
              meta: {requireAuth: false, keepAlive: false, title: ''},
              component: hot,
            },

          ]
        },
        //
        {
          path: '/login',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          component: login,
        },
        //
        {
          path: '/mobile',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          component: mobile,
        },
        //
        {
          path: '/register',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          component: register,
        },
        //
        {
          path: '/search',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          component: search,
        },
        //
        {
          path: '/attention',
          meta: {requireAuth: true, keepAlive: false, title: ''},
          component: attention,
        },
        //
        {
          path: '/quiz',
          meta: {requireAuth: true, keepAlive: false, title: ''},
          component: quiz,
        },
        //
        {
          path: '/essay',
          meta: {requireAuth: false, keepAlive: false, title: ''},
          component: essay,
        },
        //
        {
          path: '/message',
          meta: {requireAuth: true, keepAlive: false, title: ''},
          component: message,
        },
        //,
        {
          path: '/user',
          meta: {requireAuth: true, keepAlive: false, title: ''},
          component: user,
        },

      ],
    },
    //home
    {
      path: '',
      meta: {requireAuth: false, keepAlive: false, title: ''},
      redirect: home,
    },
  ]
})



