/**
 * 
 *
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 *
 */

let baseUrl = 'api/';
let routerMode = 'hash';
let imgBaseUrl = '';
let fileBaseUrl = '';


if (process.env.NODE_ENV == 'development') {
  imgBaseUrl = 'http://www.yeschains.com/';
  fileBaseUrl = '/api';

} else if (process.env.NODE_ENV == 'production') {
  baseUrl = 'http://www.yeschains.com/';
  imgBaseUrl = 'http://www.yeschains.net/';
  fileBaseUrl = 'http://www.yeschains.net/';
}

export {
  baseUrl,
  routerMode,
  fileBaseUrl,
  imgBaseUrl,
}



