//
// import Vconsole from 'vconsole';
// const vConsole = new Vconsole();
// Vue.use(vConsole);

Vue.filter('dayFmt', function (time, pattern = 'YYYY-MM-DD') {
  function timestampFormat(timestamp) {
    function zeroize(num) {
      return (String(num).length == 1 ? '0' : '') + num;
    }

    var curTimestamp = parseInt(new Date().getTime() / 1000); //
    var timestampDiff = curTimestamp - timestamp; // 

    var curDate = new Date(curTimestamp * 1000); // 
    var tmDate = new Date(timestamp * 1000);  // 

    var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
    var H = tmDate.getHours(), i = tmDate.getMinutes(), s = tmDate.getSeconds();
    if (timestampDiff < 60) { // 
      return "";
    } else if (timestampDiff < 3600) { // 
      return Math.floor(timestampDiff / 60) + "";
    } else if (curDate.getFullYear() == Y && curDate.getMonth() + 1 == m && curDate.getDate() == d) {
      return '' + zeroize(H) + ':' + zeroize(i);
    } else {
      var newDate = new Date((curTimestamp - 86400) * 1000); // 
      if (newDate.getFullYear() == Y && newDate.getMonth() + 1 == m && newDate.getDate() == d) {
        return '' + zeroize(H) + ':' + zeroize(i);
      } else if (curDate.getFullYear() == Y) {
        return zeroize(m) + '' + zeroize(d) + ' ' + zeroize(H) + ':' + zeroize(i);
      } else {
        return Y + '' + zeroize(m) + '' + zeroize(d) + ' ' + zeroize(H) + ':' + zeroize(i);
      }
    }
  }

  return timestampFormat(time);
});
//
router.beforeEach((to, from, next) => {
  store.getters.GET_LOGININFO;
  document.body.scrollTop = 0;
  // firefox
  document.documentElement.scrollTop = 0;
  // safari
  window.pageYOffset = 0;
  if (to.meta.requireAuth) {
    // 
    // console.log('');
    // next();
    if (store.state.login) {  // vuex statetoken
      next();
    } else {
      next({
        path: '/login',
        query: {redirect: to.fullPath}  // path
      })
    }
  } else {
    // console.log('');
    next();
  }
});
// this.$store.getters.GET_LOGININFO;
Vue.config.productionTip = false;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



