import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex);

const state = {
	userInfo: null, //
  // uid: null, //id
	login: false,//
	imgPath:null,//
  showSidebar:false//
};

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})



